import React from 'react';

interface Props {
    children: React.ReactNode
    set: React.Dispatch<React.SetStateAction<boolean>>
    toggle: boolean
}

/**
 * flexCommon
 * @param param0 {React.ReactNode}
 * @param set {React.Dispatch<React.SetStateAction<boolean>>}
 * @returns 
 */
export const Common: React.FC<Props> = ({ children, set }) => (
    <div className="container">
        <div className="flex">
            {children}
        </div>
        <button onClick={() => set((toggle) => !toggle)}></button>
    </div>
);

/**
 * flexCommon (Common)
 * @param children {React.ReactNode}
 * @param set {React.Dispatch<React.SetStateAction<boolean>>}
 * @returns 
 */
export const CommonWithOutFlex: React.FC<Props> = ({ children, set }) => (
    <div className="container">
        {children}
        <button onClick={() => set((toggle) => !toggle)}></button>
    </div>
);


export const CodeBlockBasic = ({ code }: { code: string }) => (
    <pre>
        <code className="language-javascript" >
            {code}
        </code>
    </pre>
);

